import { Component } from '@angular/core';
import { AlertController,IonicPage, NavController, NavParams , InfiniteScroll } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth ,NotificationProvider} from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';
import { pdfx } from '../../../providers/pdf/pdfx';





@IonicPage()
@Component({
  selector: 'page-reportcalusac',
  templateUrl: 'reportcalusac.html',
})
export class ReportcalusacPage {
titlexx:any;

public form                  : FormGroup;
public loading: boolean = true;
items: any=[];

items2: any=[];
v1:any;
v2:any;
v3:any;
v4:any;
idempresa:any;
sidist:boolean=false;
tiposus: any=[];
idempresa0:any;
public idtarifa		     : any;
public idperiodo		     : any;
public idperiodo2	  :any=[];
public vperiodo  :any=[];

public idruta		     : any;
public idtarifa2	  :any=[];
public idruta2	  :any=[];

public montog		     : any;
public trang		     : any;
public trang2		     : any;
public vtarifa  :any=[];

public vruta  :any=[];
idsuscriptor: any;
username: string = "";
nidsuscriptor: any;
nivelprx:any;
public fechaini		     = (new Date()).toISOString();
public imagen		     : any;
public fechafin		    = (new Date()).toISOString();
public gLimit: number = 50;
public PageSegmet: string = 'foods';
public gSearchQuery: string = '';

public technologies              : any = [];
public pieChartEl                : any;
public barChartEl                : any;
public lineChartEl               : any;
public chartLabels               : any    = [];
public chartValues               : any    = [];

public chartLoadingEl            : any;
public doughnutChart: any;
rp1:boolean=true;
rp2:boolean=true;
rp3:boolean=true;
rp4:boolean=true;


constructor( public Pdfx:  pdfx, public storage: Storage,public alertCtrl: AlertController,public msj: NotificationProvider, public translateService: TranslateService  ,public navCtrl: NavController,
  private _FB          : FormBuilder, public navParams: NavParams,
   public regService: Auth) {

    this.form = this._FB.group({
      fechaini     : ['', Validators.required],
      fechafin     : ['', Validators.required],
      idtarifa : ['', Validators.required],
      idruta : ['', Validators.required],
      idperiodo : ['', Validators.required]
      });





}

public clearSearchg(): void {
  this.gSearchQuery = '';
}



ionViewDidLoad() {
  this.nivelprx = this.navParams.get("nivel");




  switch (this.nivelprx) {
    case '?key=1'://relacionar usuarios con categorias
    this.rp1=false;
    this.titlexx='Horarios'
        break;
    case '?key=2'://crear
    this.titlexx='Asignación de horarios'
    this.rp2=false;
        break;
        case '?key=5'://crear
        this.rp3=false;
        this.titlexx='Estados de inscripción'
        this.trang=0;
        this.regService.getReg2b( '1'+'/'+ '2' + '/1/1/horariocalusac2','unidadplan3s').then((result) => {

  var datatt: any=[]
  datatt= result;


       for(let i =0; i < datatt.length; i++){
         this.items.push({estadopago:(datatt[i].estadopago).replace('autorizar','autoriza').replace('autoriza','autorizar'),cantidad:datatt[i].estado})

          this.trang=Number(this.trang)+Number(datatt[i].estado);
       }



        });

            break;
            case '?key=4'://crear
            this.titlexx='Ingresos bimestre'
            this.rp4=false;
            this.trang=0;
            this.trang2=0;
            this.regService.getReg2b( '1'+'/'+ '2' + '/1/1/horariocalusac22','unidadplan3s').then((result) => {

              var datatt: any=[]
              datatt= result;


                   for(let i =0; i < datatt.length; i++){
                     this.items.push({estadopago:(datatt[i].tipoa),cantidad:datatt[i].cantidad,monto:datatt[i].monto})

                      this.trang=Number(this.trang)+Number(datatt[i].cantidad);
                      this.trang2=Number(this.trang2)+Number(datatt[i].monto);
                   }



                    });

                break;
    default:

}




 this.storage.get('tiposus').then((value) => {
    this.tiposus = value.split(',');
  this.idsuscriptor=this.tiposus[3]

  this.idempresa0=this.tiposus[8]


  this.regService.getReg('todos','tipounidad3s').then((data) => {
    this.vtarifa=data;

  }, (err) => {
  this.msj.showError3('Error',err.error);
});

 });


}

ngOnInit() {
  this.storage.get('username').then((value) => {
    this.username = value;
});



}

defineChartData() : void
{
   let k : any;

   for(k in this.technologies.technologies)
   {
      var tech  =      this.technologies.technologies[k];

      this.chartLabels.push(tech.technology);
      this.chartValues.push(tech.time);

   }
}




showFilter()
{

}


twoDecimals(number) {
 return 0;
}


public loadMore(ev: InfiniteScroll) {
  this.gLimit += 50;
  setTimeout(() => ev.complete(), 1000);
}

manageGallery() : void
{
//{"createdAt": {"$gte": new Date(2012, 7, 14), "$lt": new Date(2019, 7, 15)}}
 var uno=this.idtarifa2[0];
 var dos =this.idtarifa2[1];

 if(uno)
 {}
 else
 {

    uno='TODOS';
    dos='TODOS';
 }
  let self = this;
  self.loading = true;
  this.chartLabels=[]
  this.chartValues=[]

  this.montog="0";

  this.trang="0";
  this.regService.getReg( '123' + '/billingparqueo/' + this.fechaini.substring(0, 10)+ "/" + this.fechafin.substring(0, 10) +"/"+ uno + "/" + dos + "/" + this.idempresa,'comprasaldos').then((data) => {

    this.items=data;

    for(let i =0; i <this.items.length; i++){
       this.chartLabels.push(this.items[i]._id);
       this.chartValues.push(Number(this.items[i].monto));
       this.montog=Number(this.montog)+Number(this.items[i].monto);
       this.trang=Number(this.trang)+Number(this.items[i].cantidad);
    }


//   this.createLineChart();
 // this.createghnutChart();

    self.loading = false;
}, (err) => {
  this.msj.showError3('Error',err.error);
});


}



 excludeTracks: any = [];

 presentFilter() {

}

onSelectChange(selectedValue: any) {
  this.idtarifa2=selectedValue.split(',');
  this.regService.getReg(this.idtarifa2[0]+ '/todos','unidadacademica3s').then((data) => {

    this.vruta=data;

  }, (err) => {
  this.msj.showError3('Error',err.error);
});
}
onSelectChange3(selectedValue: any) {
  this.idperiodo2=selectedValue.split(',');


  this.regService.displayPreloader('Espere un momento......')

  if(this.rp3==false || this.rp4==false)
  {

    switch (this.nivelprx) {


          case '?key=5'://crear
          this.rp3=false;
          this.titlexx='Estados de inscripción'
          this.trang=0;
          this.regService.getReg2b(  this.idtarifa2[0]+'/'+ this.idruta2[0] + '/'+ this.idperiodo2[0] +'/1/horariocalusac2aa','unidadplan3s').then((result) => {
            this.regService.hidePreloader();
    var datatt: any=[]
    datatt= result;

    this.items=[];
         for(let i =0; i < datatt.length; i++){
           this.items.push({estadopago:(datatt[i].estadopago).replace('autorizar','autoriza').replace('autoriza','autorizar'),cantidad:datatt[i].estado})

            this.trang=Number(this.trang)+Number(datatt[i].estado);
         }



          });

              break;
              case '?key=4'://crear
              this.titlexx='Ingresos bimestre'
              this.rp4=false;
              this.trang=0;
              this.trang2=0;
              this.regService.getReg2b(  this.idtarifa2[0]+'/'+ this.idruta2[0] + '/'+ this.idperiodo2[0] +'/1/horariocalusac22aa','unidadplan3s').then((result) => {
                this.regService.hidePreloader();
                var datatt: any=[]
                datatt= result;

                this.items=[]
                     for(let i =0; i < datatt.length; i++){
                       this.items.push({estadopago:(datatt[i].tipoa),cantidad:datatt[i].cantidad,monto:datatt[i].monto})

                        this.trang=Number(this.trang)+Number(datatt[i].cantidad);
                        this.trang2=Number(this.trang2)+Number(datatt[i].monto);
                     }



                      });

                  break;
      default:

  }



  }
  else
  {

  this.regService.getReg2b( this.idtarifa2[0]+'/'+ this.idruta2[0] + '/'+ this.idperiodo2[0] +'/1/horariocalusac','unidadplan3s').then((result) => {
    //  this.vpago=result;
    this.regService.hidePreloader();

    var  vdata:any=[];
    vdata=result;
    if(vdata.length==0)
    {
     this.msj.showError3('Información ','No existe periodo activo');

    }
    else
    {


    if(this.nivelprx=='?key=1')
    {
      this.Pdfx.dahorarioingles(result,this.idruta2[1]   , this.idperiodo2[0],this.idtarifa2[1]);
    }
    else
    {
      this.Pdfx.dahorarioingles2(result,this.idruta2[1]   , this.idperiodo2[0],this.idtarifa2[1]);
    }}

    }, (err) => {
      this.regService.hidePreloader();
    this.msj.showError3('Error',err.error);
    });

  }
}
onSelectChange2(selectedValue: any) {
    this.idruta2=selectedValue.split(',');

if(this.rp3==false || this.rp4==false)
{
  this.regService.getReg2b( this.idtarifa2[0]+'/'+ this.idruta2[0] + '/1/1/horariocalusacsede2','unidadplan3s').then((result) => {
    this.vperiodo=result
  });
}
else
{
  this.regService.getReg2b( this.idtarifa2[0]+'/'+ this.idruta2[0] + '/1/1/horariocalusacsede','unidadplan3s').then((result) => {
    this.vperiodo=result
  });
}






}
}



